
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';

export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
Previewer
},
directives: {
clipping: {
inserted: (el, binding) => {
const img = new Image();
img.onload = () => {
if (img.width > img.height) {
el.style.height = '100%';
} else {
el.style.width = '100%';
}
el.src = binding.value;
};
img.src = binding.value;
}
},
TransferDom
},
data() {
return {
list: []
};
},
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
this.getList();
},
methods: {
datePick(s) {
Date.prototype.toLocaleString = function() {
return this.getMonth() + 1 + '月' + this.getDate() + '日 ';
};
return new Date(s).toLocaleString();
},
getList() {
this.$http
.get('active/getParticipateActive', {
params: {
pageNum: 1,
pageSize: 200,
departmentid: this.$store.getters.user.departmentid,
userId: this.$store.getters.user.userid
}
})
.then(res => {
this.list = res.data.list;
this.list.forEach((item, index) => {
item.previewerClassName = `previewer-${index}-img`;
item.previewerOptions = {
getThumbBoundsFn(idx) {

let thumbnail = document.querySelectorAll(`.${item.previewerClassName}`)[idx];

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop;


let rect = thumbnail.getBoundingClientRect();

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };


}
};

if (!item.pictures.length) {
item.pictures = [
{ src: require('@/assets/images/preview.jpg') },
{ src: require('@/assets/images/preview1.jpg') },
{ src: require('@/assets/images/preview2.jpg') },
{ src: require('@/assets/images/preview3.jpg') }
];
}
});
});
},
preview(index, idx) {
this.$refs.previewer[index].show(idx);
},
chooseImage(length) {

const limit = 9 - length;
if (limit) {
wx.chooseImage({
count: limit, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId =>
wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
});
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).then(serverIds => {
let promiseList = [];
serverIds.map(serverId =>
promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
)
);
Promise.all(promiseList).then(result => {
let pictureIds = [];
result.map(item => pictureIds.push(item.data));
console.log(pictureIds);
});
});
}
});
}
}
}
};



